body {
  margin: 0;
  background: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidebar {
  position: fixed;
  height: 100%;
  z-index: -10;
  top: 0;
  left: 0;
  width: 20%;
}

.sidebar img {
  position: fixed;
  top: -5em;
  left: -5em;
  max-width: 50%;
  max-height: 50%;
}

.sidebar img:hover {
  cursor: pointer;
}

/* marquee */
.header {
  position: absolute;
  padding-right: 2px;
  height: 5em;
  width: 44%;
  left: 48%;
  margin-top: 2em;
  margin-right: 8em;
  background-color: #fff;
  text-align: right;
  border-bottom: 4px solid lightgrey;
}

.navbar {
  width: 10em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 2em;
  margin-bottom: 2em;
}

.navbar a {
  color: #ff2150;
  font-weight: 700;
  text-decoration: none;
}
.navbar a:hover {
  cursor: pointer;
  color: lightgrey !important;
  cursor: pointer;
}

.content {
  position: absolute;
  top: 0;
  margin-top: 14em;
  margin-left: 22%;
  padding-bottom: 4em;
  width: 70%;
  overflow: auto;
}

.cardWrapper {
  display: flex;
  align-content: space-between;
  background: white;
}

.banner {
  height: 100%;
  width: 4em;
  margin: auto;
  background-color: aquamarine;
  align-content: space-between;
}

/* blinking cursor after rotating text */
.react-rotating-text-cursor {
  animation: blinking-cursor 0.8s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s
    infinite;
}
@keyframes blinking-cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.footer {
  position: fixed;
  bottom: 0;
  height: 6em;
  width: 70%;
  z-index: -1;
  left: 22%;
  margin-right: 8em;
  text-align: right;
  padding-top: 2em;
  padding-right: 4px;
}

.footer a {
  color: #ff2150;
  font-weight: 500;
  text-decoration: none;
}

.navbar a:hover {
  cursor: pointer;
  color: lightgrey !important;
  cursor: pointer;
}
